import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
import {RemoteServiceEmpresaProvider} from '../providers/remote-service/remote-service-empresa'
import {RemoteServiceUsersProvider} from '../providers/remote-service/remote-service-users'
import {JwtTokenService} from '../providers/local-storage/jwt-token'
import {NotificationeService} from '../providers/notification/notification'

import {DefaultService} from '../providers/default/default'
import { EMPRESA } from "./pages-empresa-incluir.model";

@Component({
  selector: 'app-pages-empresa-incluir',
  templateUrl: './pages-empresa-incluir.component.html',
  styleUrls: ['./pages-empresa-incluir.component.css']
})
export class PagesEmpresaIncluirComponent implements OnInit {

  incluir:EMPRESA = new EMPRESA();
  perfil:any = [];
  titleMsg:string = 'EMPRESA';
  selectedFile : File = null;



  constructor(
    private remoteEmpresa: RemoteServiceEmpresaProvider,
    private remoteUsers: RemoteServiceUsersProvider,
    private jwtToken: JwtTokenService,
    private defaultProvider: DefaultService,
    private notification: NotificationeService,
    private router: Router,
    private route: ActivatedRoute,
  ) { 
    this.route.params.subscribe((res) => {

      console.log(res);
      
      if (res && Object.keys(res).length > 0) {
        let {id,nome,cnpj,logo,status } = res;
        status = parseInt(status);
        this.incluir ={id,nome,cnpj,logo:null,logoOld:logo,status};
      } 

    });

  }
  ngOnInit() {
    
  }

  async salvar(){
    try{
      var token = this.jwtToken.get('token');

      (!this.incluir.id) ? await this.remoteEmpresa.add(token,this.incluir) : await this.remoteEmpresa.update(token,this.incluir); 
      
      this.notification.show('top','right','success',this.titleMsg,'Empresa salva com sucesso');
      this.router.navigate(['/empresa'],{ relativeTo: this.route.parent});
    
    }
    catch(e){
      console.log(e);
      this.defaultProvider.error(e,this.titleMsg);      
    }
  }

  onFileSelected(event,campo){

    this.selectedFile = <File>event.target.files[0];
    switch (campo){
      case 'logo' : 
        this.incluir.logo = this.selectedFile ;
    }
    console.log(this.selectedFile);
  
  }


}
