import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
import {RemoteServicePerfilProvider} from '../providers/remote-service/remote-service-perfil'
import {RemoteServiceUsersProvider} from '../providers/remote-service/remote-service-users'
import {JwtTokenService} from '../providers/local-storage/jwt-token'
import {NotificationeService} from '../providers/notification/notification'

import {DefaultService} from '../providers/default/default'
import {USUARIO} from "./usuario-incluir.model";

@Component({
  selector: 'app-usuario-incluir',
  templateUrl: './usuario-incluir.component.html',
  styleUrls: ['./usuario-incluir.component.css']
})
export class UsuarioIncluirComponent implements OnInit {

  incluir:USUARIO = new USUARIO();
  perfil:any = [];
  titleMsg:string = 'USUÁRIO';


  constructor(
    private remotePerfil: RemoteServicePerfilProvider,
    private remoteUsers: RemoteServiceUsersProvider,
    private jwtToken: JwtTokenService,
    private defaultProvider: DefaultService,
    private notification: NotificationeService,
    private router: Router,
    private route: ActivatedRoute,
  ) { 
    this.route.params.subscribe((res) => {

      if (res && Object.keys(res).length > 0) {
        let password = null;
        let {id,username,email,idPerfil,status } = res;
        idPerfil = parseInt(idPerfil);
        this.incluir ={id,username,email,idPerfil,status,password };
      }

    });

  }


  ngOnInit() {
    
    this.listPerfil();
  }

  async listPerfil(){
    try{
      var token = this.jwtToken.get('token');
      let perfil:any = await this.remotePerfil.all(token);
      this.perfil = perfil;
      
    }
    catch(e){
      console.log(e);
      this.defaultProvider.error(e,this.titleMsg);      
    }

  }

  async salvar(){
    try{
      var token = this.jwtToken.get('token');
      (!this.incluir.id) ? await this.remoteUsers.incluir(token,this.incluir) : await this.remoteUsers.update(token,this.incluir); ;
      
      this.notification.show('top','right','success',this.titleMsg,'Usuário salvo com sucesso');
      this.router.navigate(['/usuarios'],{ relativeTo: this.route.parent});
    
    }
    catch(e){
      console.log(e);
      this.defaultProvider.error(e,this.titleMsg);      
    }
  }

}
