import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
import {JwtTokenService} from '../providers/local-storage/jwt-token'
import {NotificationeService} from '../providers/notification/notification'
import {DefaultService} from '../providers/default/default'
import { TREINAMENTO } from "./pages-treinamento-incluir.model";
import { RemoteServiceTreinamentoProvider } from '../providers/remote-service/remote-service-treinamento'


@Component({
  selector: 'app-pages-treinamento-incluir',
  templateUrl: './pages-treinamento-incluir.component.html',
  styleUrls: ['./pages-treinamento-incluir.component.css']
})
export class PagesTreinamentoIncluirComponent implements OnInit {

  incluir:TREINAMENTO = new TREINAMENTO();
  perfil:any = [];
  titleMsg:string = 'TREINAMENTO';
  empresas:any = [];
  selectedFile : File = null;
  unidades:any = []
  cargos:any = []
  tipoSanguinio:any = []

  constructor(
    private remoteTreinamento: RemoteServiceTreinamentoProvider,
    private jwtToken: JwtTokenService,
    private defaultProvider: DefaultService,
    private notification: NotificationeService,
    private router: Router,
    private route: ActivatedRoute,
  ) { 
    this.route.params.subscribe((res:TREINAMENTO) => {

      console.log(res);
      
      if (res && Object.keys(res).length > 0) {
        this.initialize(res)
        
      }else{
        this.initialize()
      } 

    });

  }
  ngOnInit() {

  }

  async initialize(res = null){

    if(res){

      
      this.incluir.id                   = res.id;
      this.incluir.nome                 = res.nome;
      this.incluir.descricao            = res.descricao;
      this.incluir.diasVencimento       = res.diasVencimento;
      
    }

  }

  async salvar(){
    try{
      var token = this.jwtToken.get('token');
      console.log('DADOS ENVIAR:',this.incluir);
      
      
      
      (!this.incluir.id) ? await this.remoteTreinamento.add(token,this.incluir) : await this.remoteTreinamento.update(token,this.incluir); 
      
      this.notification.show('top','right','success',this.titleMsg,'Treinamento salvo com sucesso');
      this.router.navigate(['/treinamento'],{ relativeTo: this.route.parent});
    
    }
    catch(e){
      console.log(e);
      // this.defaultProvider.error(e,this.titleMsg);      
    }
  }




}
