import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
import {JwtTokenService} from '../providers/local-storage/jwt-token'
import {NotificationeService} from '../providers/notification/notification'
import { RemoteServiceUnidadeProvider } from '../providers/remote-service/remote-service-unidade'
import {DefaultService} from '../providers/default/default'
import { FUNCIONARIO } from "./pages-funcionario-incluir.model";
import { RemoteServiceFuncionariosProvider } from '../providers/remote-service/remote-service-funcionarios'
import { RemoteServiceCargoProvider } from '../providers/remote-service/remote-service-cargo'
import { RemoteServiceTipoSanguinioProvider } from '../providers/remote-service/remote-service-tipo-sanguinio'


@Component({
  selector: 'app-pages-funcionario-incluir',
  templateUrl: './pages-funcionario-incluir.component.html',
  styleUrls: ['./pages-funcionario-incluir.component.css']
})
export class PagesFuncionarioIncluirComponent implements OnInit {

  incluir:FUNCIONARIO = new FUNCIONARIO();
  perfil:any = [];
  titleMsg:string = 'FUNCIONARIO';
  empresas:any = [];
  selectedFile : File = null;
  unidades:any = []
  cargos:any = []
  tipoSanguinio:any = []

  constructor(
    
    private remoteUnidade: RemoteServiceUnidadeProvider,
    private remoteCargo: RemoteServiceCargoProvider,
    private remoteTipoSangue: RemoteServiceTipoSanguinioProvider,
    private remoteFuncionario: RemoteServiceFuncionariosProvider,
    private jwtToken: JwtTokenService,
    private defaultProvider: DefaultService,
    private notification: NotificationeService,
    private router: Router,
    private route: ActivatedRoute,
  ) { 
    this.route.params.subscribe((res:FUNCIONARIO) => {

      console.log(res);
      
      if (res && Object.keys(res).length > 0) {
        this.initialize(res)
        
      }else{
        this.initialize()
      } 

    });

  }
  ngOnInit() {

  }

  async initialize(res = null){
   await this.listUnidade();
   await this.listCargo();
   await this.listTipoSangue();
    if(res){

      this.incluir.idUnidade            = Number(res.idUnidade);
      this.incluir.id                   = res.id;
      this.incluir.nome                 = res.nome;
      this.incluir.rg                   = res.rg;
      this.incluir.emissor              = res.emissor;
      this.incluir.dataAdmissao         = res.dataAdmissao;
      this.incluir.dataNascimento       = res.dataNascimento;
      this.incluir.dataVencimentoAso    = res.dataVencimentoAso;
      this.incluir.cnh                  = res.cnh;
      this.incluir.categoriaCnh         = res.categoriaCnh;
      this.incluir.validadeCnh          = res.validadeCnh;
      this.incluir.idTipoSaguinio       = Number(res.idTipoSaguinio);
      this.incluir.idCargo              = Number(res.idCargo);
      this.incluir.tercerizado          = res.tercerizado == 'S' ? 1 : 0;
      this.incluir.empresaTerceira      = res.empresaTerceira;
      this.incluir.fotoOld              = res.foto;
      this.incluir.status               = Number(res.status);

      
    }

  }


  async salvar(){
    try{
      var token = this.jwtToken.get('token');
      console.log('DADOS ENVIAR:',this.incluir);
      this.incluir.dataAdmissao       = await this.defaultProvider.formatYYYYMMDD(this.incluir.dataAdmissao);
      this.incluir.dataNascimento     = await this.defaultProvider.formatYYYYMMDD(this.incluir.dataNascimento);
      this.incluir.dataVencimentoAso  = await this.defaultProvider.formatYYYYMMDD(this.incluir.dataVencimentoAso);
      this.incluir.validadeCnh        = await this.defaultProvider.formatYYYYMMDD(this.incluir.validadeCnh);
      
      this.incluir.tercerizado = this.incluir.tercerizado == '1' ? 'S' : 'N';
      (!this.incluir.id) ? await this.remoteFuncionario.add(token,this.incluir) : await this.remoteFuncionario.update(token,this.incluir); 
      
      this.notification.show('top','right','success',this.titleMsg,'Funcionário salvo com sucesso');
      this.router.navigate(['/funcionario'],{ relativeTo: this.route.parent});
    
    }
    catch(e){
      console.log(e);
      // this.defaultProvider.error(e,this.titleMsg);      
    }
  }

  onFileSelected(event,campo){

    this.selectedFile = <File>event.target.files[0];
    switch (campo){
      case 'foto' : 
        this.incluir.foto = this.selectedFile ;
    }
    console.log(this.selectedFile);
  
  }

  async listUnidade(){
    try{
      var token = this.jwtToken.get('token');
      let lista:any = await this.remoteUnidade.all(token);
      this.unidades =  lista;
      
    
    }
    catch(e){
      console.log(e);
      this.defaultProvider.error(e,this.titleMsg);      
    }

  }

  async listCargo(){
    try{
      var token = this.jwtToken.get('token');
      let lista:any = await this.remoteCargo.all(token);
      this.cargos =  lista;
    
    }
    catch(e){
      console.log(e);
      this.defaultProvider.error(e,this.titleMsg);      
    }

  }

  async listTipoSangue(){
    try{
      var token = this.jwtToken.get('token');
      let lista:any = await this.remoteTipoSangue.all(token);
      this.tipoSanguinio =  lista;
    
    }
    catch(e){
      console.log(e);
      this.defaultProvider.error(e,this.titleMsg);      
    }

  }



}
