import { Component, OnInit } from '@angular/core';
import { RemoteServiceUsersProvider } from '../providers/remote-service/remote-service-users'
import {JwtTokenService} from '../providers/local-storage/jwt-token'
// import {NotificationeService} from '../../providers/notification/notification'
import { Router,ActivatedRoute } from '@angular/router';
import {DefaultService} from '../providers/default/default'
@Component({
  selector: 'app-usuario',
  templateUrl: './usuarios.component.html',
  styleUrls: ['./usuarios.component.css']
})
export class UsuarioComponent implements OnInit {

  titleMsg:string = 'USUÁRIO';
  lista:Array<Object> = [];

  constructor(
    private remoteUsers: RemoteServiceUsersProvider,
    private jwtToken: JwtTokenService,
    private defaultProvider: DefaultService,
    private router: Router,
    private route: ActivatedRoute,
    // private notifification: NotificationeService,
  ) { }

  ngOnInit() {
    this.list();
    
  }


  async list(){
    try{
      var token = this.jwtToken.get('token');
      let lista:any = await this.remoteUsers.usuarioLista(token);
      this.lista = lista;
      
      // this.notifification.show('top','right','success',this.titleMsg,'TESTE');
    
    }
    catch(e){
      console.log(e);
      this.defaultProvider.error(e,this.titleMsg);      
    }

  }

  incluir(item = null){

    item = !item ? {} : item;
    this.router.navigate(['/usuarios-incluir', item],{ relativeTo: this.route.parent, skipLocationChange: true   });
    
  
  }
  config(item ){

    
    this.router.navigate(['/usuarios-configurar', {dados:JSON.stringify(item)}],{ relativeTo: this.route.parent, skipLocationChange: true   });
    
  
  }

}
