import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
import {RemoteServiceEmpresaProvider} from '../providers/remote-service/remote-service-empresa'
import {RemoteServiceUsersProvider} from '../providers/remote-service/remote-service-users'
import {JwtTokenService} from '../providers/local-storage/jwt-token'
import {NotificationeService} from '../providers/notification/notification'

import {DefaultService} from '../providers/default/default'
import {USUARIO} from "./usuarios-configurar.model";




@Component({
  selector: 'app-usuarios-configurar',
  templateUrl: './usuarios-configurar.component.html',
  styleUrls: ['./usuarios-configurar.component.css']
})
export class UsuarioConfigurarComponent implements OnInit {

  incluir:USUARIO = new USUARIO();
  empresas:any = [];
  titleMsg:string = 'USUÁRIO';
  username:any = null;
  unidadesByEmpresa:any = [];
  empresasHas:any = [];
  unidadesHas:any = [];
  carregaSelecionado: boolean = false;


  constructor(
    private remoteEmpresa: RemoteServiceEmpresaProvider,
    private remoteUsers: RemoteServiceUsersProvider,
    private jwtToken: JwtTokenService,
    private defaultProvider: DefaultService,
    private notification: NotificationeService,
    private router: Router,
    private route: ActivatedRoute,
  ) { 
    this.route.params.subscribe((res) => {

      if (res.dados && Object.keys(res.dados).length > 0) {
        
        let json = JSON.parse(res.dados);
        let {username,idPerfil,empresasHas, unidadesHas} = json;
        this.username = username
        this.empresasHas = empresasHas
        this.unidadesHas = unidadesHas
        this.incluir.usuario = json;
        
        idPerfil = parseInt(idPerfil);
        
      }

    });

  }


  ngOnInit() {
    
    this.listEmpresa();
  }

  async listEmpresa(){
    try{
      var token = this.jwtToken.get('token');
      let empresa:any = await this.remoteEmpresa.all(token);
      this.empresas = empresa;

      if(this.empresasHas.length > 0){
        this.empresasHas.map( value => {
          let registro = this.empresas.find(emp => value.idEmpresa == emp.id )
          if(registro){
            this.incluir.empresa.push(registro.id)
          }
          
          

        })
      }

      await this.listUnidades('inicial');
      
    }
    catch(e){
      console.log(e);
      this.defaultProvider.error(e,this.titleMsg);      
    }

  }


  async listUnidades(origem){

    
    try{
      var token = this.jwtToken.get('token');
      
      let unidades:any = await this.remoteEmpresa.listUnidades(token,this.incluir.empresa);
      this.unidadesByEmpresa = unidades;
      
      this.carregaSelecionado = false;
      if(origem == 'inicial'){

        if(this.unidadesHas.length > 0){
          this.carregaSelecionado = true;
          this.unidadesHas.map( value => {
            this.unidadesByEmpresa.map(emp => {
              let registro = emp.unidades.find(uni => uni.id == value.idUnidade )
              console.log(registro);
              if(registro){
                this.incluir.unidade.push(registro)
              }
              
            })
   
            
            
  
          })
        }

      }
      
    }
    catch(e){
      console.log(e);
      this.defaultProvider.error(e,this.titleMsg);      
    }
  }

  selectUnidade(e,valor){

    if(valor.length == 0){
      let index = this.incluir.unidade.findIndex(value => value.id == e.id);
      this.incluir.unidade.splice(index,1);
    }else{
      this.incluir.unidade.push(valor[0].value);
    }
    
    
  }

  clearUnidade(){
    this.carregaSelecionado = false
    this.incluir.unidade = [];
    
  }

  async salvar(){
    try{
      var token = this.jwtToken.get('token');
      await this.remoteUsers.config(token,this.incluir);
      
      this.notification.show('top','right','success',this.titleMsg,'Configuração realizada com sucesso');
      this.router.navigate(['/usuarios'],{ relativeTo: this.route.parent});
    
    }
    catch(e){
      console.log(e);
      this.defaultProvider.error(e,this.titleMsg);      
    }
  }

}
