import { Component, OnInit ,HostListener} from '@angular/core';
import { RemoteServiceCargoProvider } from '../providers/remote-service/remote-service-cargo'
import {JwtTokenService} from '../providers/local-storage/jwt-token'
// import {NotificationeService} from '../../providers/notification/notification'
import { Router,ActivatedRoute } from '@angular/router';
import {DefaultService} from '../providers/default/default'
import {RemoteServiceFuncionariosProvider} from '../providers/remote-service/remote-service-funcionarios'
import { RemoteServiceUnidadeProvider } from '../providers/remote-service/remote-service-unidade'

// import {MatTableDataSource} from '@angular/material/table';


@Component({
  selector: 'app-pages-funcionario',
  templateUrl: './pages-funcionario.component.html',
  styleUrls: ['./pages-funcionario.component.css']

})




export class PagesFuncionarioComponent implements OnInit {

  titleMsg:string = 'FUNCIONARIO';
  lista:any = [];
  listaFull:any = [];
  empresas:any = [];
  empresa:string= '1';
  unidades:any = [];
  idUnidade:any = null;
  tableActive = window.innerWidth;

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.tableActive = window.innerWidth;
  }

  displayedColumns: string[] = ['id', 'nome', 'foto','nascimento','vencimentoAso','tercerizado','status','action'];
  
  constructor(
    private remoteFuncionarios: RemoteServiceFuncionariosProvider,
    private remoteUnidade: RemoteServiceUnidadeProvider,
    private jwtToken: JwtTokenService,
    private defaultProvider: DefaultService,
    private router: Router,
    private route: ActivatedRoute
    
    // private notifification: NotificationeService,
  ) { }

  ngOnInit() {
    // this.list();
    // this.listEmpresa();
    this.listUnidade();
    // console.log(window.innerWidth)
    // this.tableActive = window.innerWidth < 940 ? false : true;

  }


  async listUnidade(){
    try{
      var token = this.jwtToken.get('token');
      let lista:any = await this.remoteUnidade.all(token);
      this.unidades =  lista;
      
      
    
    }
    catch(e){
      console.log(e);
      this.defaultProvider.error(e,this.titleMsg);      
    }

  }

  async list(){
    try{
      var token = this.jwtToken.get('token');
      let lista:any = await this.remoteFuncionarios.byUnidade(this.idUnidade,token);
      this.lista =  lista;
      this.listaFull =  lista;
      
    
    }
    catch(e){
      console.log(e);
      this.defaultProvider.error(e,this.titleMsg);      
    }

  }

  incluir(item = null){

    item = !item ? {} : item;
    this.router.navigate(['/funcionario-incluir', item],{ relativeTo: this.route.parent, skipLocationChange: true   });
    
  
  }


  applyFilter(event: Event) {
    
    const filterValue = (event.target as HTMLInputElement).value;
    
    this.lista = !filterValue ? 
                  this.listaFull : 
                  this.lista.filter(value =>{
                    return (
                            value.nome.toLowerCase().includes(filterValue.toLowerCase()) || 
                            value.id == filterValue
                            )
                  })
    // this.lista.filter = filterValue.trim().toLowerCase();
  }



}
