import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
import {JwtTokenService} from '../providers/local-storage/jwt-token'
import {NotificationeService} from '../providers/notification/notification'
import { RemoteServiceCargoProvider } from '../providers/remote-service/remote-service-cargo'
import {DefaultService} from '../providers/default/default'
import { CARGO } from "./pages-cargo-incluir.model";

@Component({
  selector: 'app-pages-cargo-incluir',
  templateUrl: './pages-cargo-incluir.component.html',
  styleUrls: ['./pages-cargo-incluir.component.css']
})
export class PagesCargoIncluirComponent implements OnInit {

  incluir:CARGO = new CARGO();
  perfil:any = [];
  titleMsg:string = 'CARGO';
  empresas:any = [];

  constructor(
    
    private remoteCargo: RemoteServiceCargoProvider,
    private jwtToken: JwtTokenService,
    private defaultProvider: DefaultService,
    private notification: NotificationeService,
    private router: Router,
    private route: ActivatedRoute,
  ) { 
    this.route.params.subscribe((res) => {

      console.log(res);
      
      if (res && Object.keys(res).length > 0) {
        let {id,nome,status } = res;
        status = parseInt(status);
        this.incluir ={id,nome,status};
      } 

    });

  }
  ngOnInit() {
    
  }

  async salvar(){
    try{
      var token = this.jwtToken.get('token');

      (!this.incluir.id) ? await this.remoteCargo.add(token,this.incluir) : await this.remoteCargo.update(token,this.incluir); 
      
      this.notification.show('top','right','success',this.titleMsg,'Cargo salva com sucesso');
      this.router.navigate(['/cargo'],{ relativeTo: this.route.parent});
    
    }
    catch(e){
      console.log(e);
      this.defaultProvider.error(e,this.titleMsg);      
    }
  }




}
