import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
import {RemoteServicePerfilProvider} from '../providers/remote-service/remote-service-perfil'
import {RemoteServiceUsersProvider} from '../providers/remote-service/remote-service-users'
import {JwtTokenService} from '../providers/local-storage/jwt-token'
import {NotificationeService} from '../providers/notification/notification'

import {DefaultService} from '../providers/default/default'
import { PERFIL } from "./pages-perfil-incluir.model";

@Component({
  selector: 'app-pages-perfil-incluir',
  templateUrl: './pages-perfil-incluir.component.html',
  styleUrls: ['./pages-perfil-incluir.component.css']
})
export class PagesPerfilIncluirComponent implements OnInit {

  incluir:PERFIL = new PERFIL();
  perfil:any = [];
  titleMsg:string = 'PERFIL';


  constructor(
    private remotePerfil: RemoteServicePerfilProvider,
    private remoteUsers: RemoteServiceUsersProvider,
    private jwtToken: JwtTokenService,
    private defaultProvider: DefaultService,
    private notification: NotificationeService,
    private router: Router,
    private route: ActivatedRoute,
  ) { 
    this.route.params.subscribe((res) => {

      console.log(res);
      
      if (res) {
        let {id,nome,status } = res;
        status = parseInt(status);
        this.incluir ={id,nome,status};
      } 

    });

  }


  ngOnInit() {
    
  }


  async salvar(){
    try{
      var token = this.jwtToken.get('token');
      (!this.incluir.id) ? await this.remotePerfil.add(token,this.incluir) : await this.remotePerfil.update(token,this.incluir); 
      
      this.notification.show('top','right','success',this.titleMsg,'Perfil salvo com sucesso');
      this.router.navigate(['/perfil'],{ relativeTo: this.route.parent});
    
    }
    catch(e){
      console.log(e);
      this.defaultProvider.error(e,this.titleMsg);      
    }
  }

}
