import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from "@angular/router";
import {RemoteServiceEmpresaProvider} from '../providers/remote-service/remote-service-empresa'

import {JwtTokenService} from '../providers/local-storage/jwt-token'
import {NotificationeService} from '../providers/notification/notification'
import { RemoteServiceUnidadeProvider } from '../providers/remote-service/remote-service-unidade'
import {DefaultService} from '../providers/default/default'
import { UNIDADE } from "./pages-unidade-incluir.model";

@Component({
  selector: 'app-pages-unidade-incluir',
  templateUrl: './pages-unidade-incluir.component.html',
  styleUrls: ['./pages-unidade-incluir.component.css']
})
export class PagesUnidadeIncluirComponent implements OnInit {

  incluir:UNIDADE = new UNIDADE();
  perfil:any = [];
  titleMsg:string = 'UNIDADE';
  empresas:any = [];



  constructor(
    private remoteEmpresa: RemoteServiceEmpresaProvider,
    private remoteUnidade: RemoteServiceUnidadeProvider,
    private jwtToken: JwtTokenService,
    private defaultProvider: DefaultService,
    private notification: NotificationeService,
    private router: Router,
    private route: ActivatedRoute,
  ) { 
    this.route.params.subscribe((res) => {

      console.log(res);
      
      if (res && Object.keys(res).length > 0) {
        let {id,nome,idEmpresa,status } = res;
        status = parseInt(status);
        this.incluir ={id,nome,idEmpresa,status};
      } 

    });

  }
  ngOnInit() {
    this.listEmpresa();
  }

  async salvar(){
    try{
      var token = this.jwtToken.get('token');

      (!this.incluir.id) ? await this.remoteUnidade.add(token,this.incluir) : await this.remoteUnidade.update(token,this.incluir); 
      
      this.notification.show('top','right','success',this.titleMsg,'Unidade salva com sucesso');
      this.router.navigate(['/unidade'],{ relativeTo: this.route.parent});
    
    }
    catch(e){
      console.log(e);
      this.defaultProvider.error(e,this.titleMsg);      
    }
  }

  async listEmpresa(){
    try{
      var token = this.jwtToken.get('token');
      let lista:any = await this.remoteEmpresa.all(token);
      this.empresas = lista;
      
    
    }
    catch(e){
      console.log(e);
      this.defaultProvider.error(e,this.titleMsg);      
    }

  }



}
