import { Component, OnInit } from '@angular/core';
import { RemoteServiceCargoProvider } from '../providers/remote-service/remote-service-cargo'
import {JwtTokenService} from '../providers/local-storage/jwt-token'
// import {NotificationeService} from '../../providers/notification/notification'
import { Router,ActivatedRoute } from '@angular/router';
import {DefaultService} from '../providers/default/default'
import {RemoteServiceEmpresaProvider} from '../providers/remote-service/remote-service-empresa'
// import {MatTableDataSource} from '@angular/material/table';


@Component({
  selector: 'app-pages-cargo',
  templateUrl: './pages-cargo.component.html',
  styleUrls: ['./pages-cargo.component.css']

})



export class PagesCargoComponent implements OnInit {

  titleMsg:string = 'CARGO';
  lista:any = [];
  listaFull:any = [];
  empresas:any = [];
  empresa:string= '1';

  displayedColumns: string[] = ['id', 'nome', 'status','action'];
  
  constructor(
    private remoteCargo:  RemoteServiceCargoProvider,
    private remoteEmpresa: RemoteServiceEmpresaProvider,
    private jwtToken: JwtTokenService,
    private defaultProvider: DefaultService,
    private router: Router,
    private route: ActivatedRoute
    
    // private notifification: NotificationeService,
  ) { }

  ngOnInit() {
    this.list();
    this.listEmpresa();
  }


  async list(){
    try{
      var token = this.jwtToken.get('token');
      let lista:any = await this.remoteCargo.all(token);
      this.lista =  lista;
      this.listaFull =  lista;
      
    
    }
    catch(e){
      console.log(e);
      this.defaultProvider.error(e,this.titleMsg);      
    }

  }

  incluir(item = null){

    item = !item ? {} : item;
    this.router.navigate(['/cargo-incluir', item],{ relativeTo: this.route.parent, skipLocationChange: true   });
    
  
  }

  async listEmpresa(){
    try{
      var token = this.jwtToken.get('token');
      let lista:any = await this.remoteEmpresa.all(token);
      this.empresas = lista;
      
    
    }
    catch(e){
      console.log(e);
      this.defaultProvider.error(e,this.titleMsg);      
    }

  }



  applyFilter(event: Event) {
    
    const filterValue = (event.target as HTMLInputElement).value;
    
    this.lista = !filterValue ? 
                  this.listaFull : 
                  this.lista.filter(value =>{
                    return (
                            value.nome.toLowerCase().includes(filterValue.toLowerCase()) || 
                            value.id == filterValue
                            )
                  })
    // this.lista.filter = filterValue.trim().toLowerCase();
  }



}
